<template>
  <div class="refund_analysis">
    <div class="top_option">
      <div>
        <el-form :inline="true" :model="form_detail" class="demo-form-inline">
          <!-- <el-form-item label="渠道">
            <el-select
              size="mini"
              v-model="form_detail.channel"
              placeholder="请选择渠道"
            >
              <el-option label="全部" value="all"></el-option>
              <el-option
                v-for="(item, i) in channels"
                :key="i"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </el-form-item> -->

          <el-form-item label="国家">
            <el-select
              size="mini"
              @change="changeOpt('country')"
              v-model="form_detail.country"
              placeholder="请选择国家"
            >
              <el-option label="全部" value="all"></el-option>
              <el-option
                v-for="(item, i) in countries"
                :key="i"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="站点">
            <el-select
              size="mini"
              @change="changeOpt('store')"
              v-model="form_detail.site"
              placeholder="请选择站点"
            >
              <el-option label="全部" value="all"></el-option>
              <el-option
                v-for="(item, i) in stores"
                :key="i"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="模糊搜索">
            <el-input
              size="mini"
              v-model="searchVal"
              placeholder="请输入字段搜索"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" size="mini" @click="search"
              >搜索</el-button
            >
          </el-form-item>

          <el-form-item>
            <el-button
              type="primary"
              size="mini"
              @click="go_route('退货时间监控')"
              >退货时间监控</el-button
            >
          </el-form-item>
        </el-form>
      </div>
    </div>
    <div class="table_con">
      <el-table
        border
        :data="tableData"
        style="width: 100%"
        :row-style="tableRowStyle"
        v-loading="loading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.3)"
      >
        <el-table-column type="index" label="序号" :index="indexMethod">
        </el-table-column>
        <el-table-column prop="platform" label="渠道" width="80">
        </el-table-column>
        <el-table-column prop="site" label="站点" width="80"> </el-table-column>
        <el-table-column prop="country" label="国家" width="80">
        </el-table-column>
        <el-table-column prop="order_id" label="订单号" width="180">
        </el-table-column>
        <el-table-column prop="product_type" label="类型"> </el-table-column>
        <el-table-column prop="product_code" label="产品编码" width="150">
        </el-table-column>
        <el-table-column prop="product_name" label="品名" width="150">
        </el-table-column>
        <el-table-column prop="sku" label="sku" width="200"> </el-table-column>
        <el-table-column prop="warehouse_type" label="仓库类型">
        </el-table-column>
        <el-table-column prop="warehouse_code" label="仓库编码">
        </el-table-column>
        <el-table-column prop="warehouse_name" label="仓库名称">
        </el-table-column>
        <el-table-column prop="purchase_date" label="下单时间">
        </el-table-column>
        <el-table-column prop="date" label="退货时间" width="150">
        </el-table-column>
        <!-- <el-table-column prop="refund_way" label="退货方式"> </el-table-column> -->
        <el-table-column prop="detaild_reson" label="详细退货原因" width="250">
        </el-table-column>
        <el-table-column prop="due_person" label="处理人"> </el-table-column>
      </el-table>
      <el-pagination
        @current-change="changePage"
        :current-page.sync="currentPage"
        :page-size="50"
        layout="total, prev, pager, next"
        :total="totalNum"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchVal: "",
      loading: false,
      currentPage: 1,
      totalNum: 0,
      form_detail: {
        channel: "all",
        site: "all",
        country: "all",
        type: "all",
        product_code: "all",
        product_name: "all",
        sku: "all",
        warehouse_type: "all",
        warehouse_name: "all",
        // refund_date: "", // 退单
        // purchase_date: "", // 下单
        refund_way: "all",
        refund_reason: "all",
        refund_type: "all",
        refund_reason: "all",
        deal_person: "all"
      },
      ordertime1: "",
      ordertime2: "",
      ordertime3: "",
      ordertime4: "",
      tableData: [{}],
      channels: [],
      stores: [],
      countries: [],
      types: [],
      product_codes: [],
      product_names: [],
      skus: [],
      warehouse_types: [],
      warehouse_names: [],
      refund_reasons: [],
      refund_ways: [],
      isDisabled1: false,
      isDisabled2: false,
      isDisabled3: false,
      isDisabled4: false,
      // 数据
      data1: [],
      data2: []
    };
  },
  methods: {
    indexMethod(index) {
      return index + 1 + 50 * (this.currentPage - 1);
    },
    changePage(val) {
      this.changePage = val;
      // 请求
      this.search();
    },
    changeOpt(val) {
      if (val === "country") {
        this.stores = [];
        this.form_detail.station = "all";
        if (this.form_detail.country !== "all") {
          let url = "server/personnel_management/get_sign_area/";
          this.$axios
            .get(this._url2 + url, {
              params: {
                country: this.form_detail.country
              }
            })
            .then(res => {
              this.stores = res.data.data;
              this.form_detail.site = this.stores[0];
            });
        }
      }
    },
    search() {
      this.getInitTable();
    },
    go_route(val) {
      if (val === "折线分析") {
        this.$router.push("/ChartAnalysis");
      }
      if (val === "退货时间监控") {
        this.$router.push("/ReturnTimeMonitor");
      }
    },
    change1(value) {
      if (!value) {
        console.log("没有值了");
        this.isDisabled2 = false;
        this.isDisabled1 = false;
      } else {
        this.isDisabled2 = true;
      }
    },
    change2(value) {
      if (!value) {
        this.isDisabled2 = false;
        this.isDisabled1 = false;
      } else {
        this.isDisabled1 = true;
      }
    },
    change3(value) {
      if (!value) {
        console.log("没有值了");
        this.isDisabled4 = false;
        this.isDisabled3 = false;
      } else {
        this.isDisabled4 = true;
      }
    },
    change4(value) {
      if (!value) {
        this.isDisabled4 = false;
        this.isDisabled3 = false;
      } else {
        this.isDisabled3 = true;
      }
    },
    tableRowStyle({ row, column, rowIndex, columnIndex }) {
      return "background-color:rgba(255,255,255,.9)";
    },
    getInitTable() {
      let queryObj = {};
      if (this.form_detail.site === "all") {
        queryObj["site"] = "";
      } else {
        queryObj["site"] = this.form_detail.site;
      }
      if (this.form_detail.site === "all") {
        queryObj["country"] = "";
      } else {
        queryObj["country"] = this.form_detail.country;
      }
      queryObj["condition"] = this.searchVal;
      queryObj["page"] = this.currentPage;
      let url = this._url2 + "server/order_management/return_goods/";
      this.$axios
        .get(url, {
          params: queryObj
        })
        .then(res => {
          console.log(res);
          this.tableData = res.data.re_data || [];
          this.totalNum = 0;
          if (
            res.data.re_count_data.length > 0 &&
            res.data.re_count_data[0].count_data
          ) {
            this.totalNum = res.data.re_count_data[0].count_data;
          }
        });
    }
  },
  created() {
    let url = "server/personnel_management/get_sign_country/";
    this.$axios.get(this._url2 + url).then(res => {
      this.channels.push(res.data.data1);
      this.countries = res.data.data;
    });
    this.getInitTable();
  }
};
</script>
<style>
.refund_analysis .el-form-item {
  margin-bottom: 0;
}
</style>
<style scoped>
.refund_analysis {
  padding-top: 30px;
}

.refund_analysis >>> .el-form-item__label {
  color: #fff;
}
.top_option {
  background: rgba(255, 255, 255, 0.9);
  padding-left: 22px;
  border-radius: 5px;
  margin-bottom: 5px;
  /* padding-top: 22px; */
}
.top_option >>> .el-form-item__label {
  color: #000;
}
</style>
<style>
.refund_analysis .el-pagination {
  background: rgba(255, 255, 255, 0.9);
}
</style>



